import React, { Component } from "react";
import MainTitle from "../components/pageTitle";
import Button from "../components/button";
import GameField from "../modules/gameField";
import "../index.css";
import { CountdownCircleTimer } from "react-countdown-circle-timer";
import { Redirect } from "react-router";

const timeInteval = parseInt(localStorage.time) * 1000;
class Game extends Component {
  constructor(props) {
    super(props);
    this.state = {
      endGame: false,
    };

    this.setRedirect = this.setRedirect.bind(this);
    // this.renderRedirect = this.renderRedirect.bind(this);
  }

  setRedirect() {
    console.log("red redirect");
    this.setState({
      endGame: true,
    });
  }

  render() {
    if (this.state.endGame) {
      return <Redirect to="/congrats" />;
    }
    return (
      <div className="game-wrapper">
        <div className="game-top-panel">
          <MainTitle label="Mine Hard Like the Devil&nbsp;üëπ" />

          <CountdownCircleTimer
            isPlaying
            duration={parseInt(localStorage.time)}
            colors={[["#f39c12", 0.33], ["#f39c12", 0.33], ["#f39c12"]]}
            size={60}
            strokeWidth={0}
            onComplete={this.setRedirect}
          >
            {({ remainingTime }) => remainingTime}
          </CountdownCircleTimer>
        </div>

        <GameField />
      </div>
    );
  }
}

export default Game;
